body {
  color: #fff;
  background: linear-gradient(#0d001f, #090027, #1a0033);
  margin: 0;
  padding: 0;
  font-family: Inter, sans-serif;
}

header {
  background: linear-gradient(to right, #140033, #250050);
  justify-content: space-between;
  align-items: center;
  padding: 16px 32px;
  display: flex;
  box-shadow: 0 4px 12px #0009;
}

header h1 {
  font-size: 1.75rem;
  font-weight: 700;
}

aside {
  background: linear-gradient(#12003a, #1a0055);
  width: 260px;
  padding: 24px;
  box-shadow: 4px 0 12px #0006;
}

aside h2 {
  margin-bottom: 24px;
  font-size: 1.5rem;
}

aside .tab-button {
  color: #a1a1c1;
  background: none;
  border-radius: 10px;
  align-items: center;
  gap: 16px;
  padding: 12px 16px;
  font-size: 1rem;
  font-weight: 600;
  transition: background .3s, transform .2s;
  display: flex;
}

aside .tab-button:hover, aside .tab-button.active {
  color: #fff;
  background: linear-gradient(135deg, #6b21a8, #b83280);
  transform: translateX(8px);
}

main {
  flex-grow: 1;
  padding: 32px;
}

main h2 {
  color: #a29bff;
  margin-bottom: 24px;
  font-size: 2.25rem;
  font-weight: bold;
}

main .grid {
  grid-template-columns: repeat(2, 1fr);
  gap: 24px;
  display: grid;
}

main .grid-single {
  grid-template-columns: 1fr;
}

.card {
  backdrop-filter: blur(12px);
  background: #ffffff0d;
  border: 1px solid #ffffff1a;
  border-radius: 16px;
  padding: 24px;
  transition: transform .3s, box-shadow .3s;
  box-shadow: 0 8px 24px #0006;
}

.card:hover {
  transform: translateY(-4px);
  box-shadow: 0 12px 28px #000c;
}

.card h3 {
  margin-bottom: 16px;
  font-size: 1.5rem;
  font-weight: bold;
}

.card p {
  color: #d1d1e9;
  margin-bottom: 8px;
  font-size: 1rem;
}

button {
  color: #fff;
  background: linear-gradient(135deg, #6a21ab, #9333ee);
  border: none;
  border-radius: 12px;
  padding: 12px 20px;
  font-weight: bold;
  transition: transform .3s, background .3s;
  display: inline-block;
}

button:hover {
  background: linear-gradient(135deg, #9333ee, #6a21ab);
  transform: scale(1.05);
  box-shadow: 0 6px 16px #9333ee99;
}

input {
  color: #fff;
  background: #00003233;
  border: 1px solid #0000;
  border-radius: 8px;
  outline: none;
  width: 100%;
  padding: 12px 16px;
  transition: border-color .3s, box-shadow .3s;
}

input:focus {
  border-color: #6a21ab;
  box-shadow: 0 0 8px #6a21abcc;
}

.transaction-table {
  border-collapse: collapse;
  background: #ffffff0d;
  border-radius: 16px;
  width: 100%;
  margin-top: 24px;
  overflow: hidden;
  box-shadow: 0 4px 12px #0009;
}

.transaction-table th, .transaction-table td {
  text-align: left;
  color: #d1d1e9;
  padding: 12px;
}

.transaction-table th {
  background: #ffffff1a;
  font-weight: 700;
}

.transaction-table tr:hover {
  background: #ffffff14;
}
